import {mapGetters, mapState} from 'vuex';
export default {
	name: "app-sell-tip",
	components:{},
	props: {
		time: {
			type: Number,
			default() {
				return 0;
			}
		}
	},
	data() {
		return {
			timer: null,
			timeLog: 0
		}
	},
	watch:{
		time: {
			handler() {
				let timelog = this.time;
				if (timelog <= 0) {
					return;
				}
				let day = parseInt((timelog / 60 / 60 / 24));
				let hour = parseInt((timelog / 60 / 60) % 24);
				let min = parseInt((timelog / 60) % 60);
				let sec = parseInt((timelog) % 60);
				this.timer = {
					day: day,
					hour: hour < 10 ? "0" + hour : hour,
					min: min < 10 ? "0" + min : min,
					sec: sec < 10 ? "0" + sec : sec
				};
				setTimeout(() => {
				    timelog -= 1;
					this.$emit('changeTime', timelog);
				}, 1000);
			},
			immediate: true
		}
	},
	computed:{
		...mapState({
			isTip: state => state.mallConfig.mall.setting.is_remind_sell_time
		}),
		content() {
			let str = '即将开售';
			if (this.isTip == 1) {
				str += '，记得设置提醒'
			}
			return str;
		}
	}
}
